.wrapper {
  max-width: 600px;
  width: 100%;
  margin-top: 100px;
  border: 1px solid transparent;
}

.list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 16px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.filling {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 0;
  margin: 16px 0;
  overflow-y: scroll;
  max-height: 465px;
}

.item_bun {
  box-sizing: border-box;
  display: flex;
  justify-content: flex-end;
}

.order {
  display: flex;
  justify-content: flex-end;
  gap: 40px;
}

.price {
  display: flex;
  align-items: center;
  gap: 10px;
}

.title {
  font-family: 'Jet Brains Mono';
  font-size: 32px;
  font-weight: bold;
  line-height: 1.1;
}

.drop {
  margin: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: var(--common-border-radius-s);
  background: var(--background-element);
}

.border_color {
  border-color: lightgreen;
}